* {
  font-family: "Space Mono", monospace;
  padding: 0;
  margin: 0;
  font-size: 24px;
}
.amount {
  display: flex;
  justify-content: space-between;
}

.first {
  color: hsl(185, 41%, 84%);
  margin: 0;
}
.second {
  color: hsl(184, 14%, 56%) !important ;
  font-size: 15px;
}
.third {
  color: hsl(172, 67%, 45%) !important;
  font-size: 40px;
}

.container {
  margin-top: 50vh;
  margin-left: 50vw;
  transform: translate(-50%, -50%);
  box-shadow: 5px 20px 20px hsl(184, 14%, 56%) !important;
}
button {
  background-color: hsl(183, 100%, 15%) !important;
  color: white !important;
  border: none;
  border-radius: 5px !important;
  height: 56px;
  cursor: pointer;
}
button:focus {
  background-color: hsl(172, 67%, 45%) !important ;
}
.reset {
  background-color: hsl(172, 67%, 45%) !important;
  color: hsl(183, 100%, 15%) !important;
  width: 80%;
  margin-left: 10%;
  margin-top: 30%;
}
.container {
  background-color: white;
  padding: 30px;
  border-radius: 20px;
}
input {
  border: none !important;
  background-color: hsl(189, 41%, 97%) !important;
  border-radius: 5px !important;
}
span {
  border: none !important;
  background-color: hsl(189, 41%, 97%) !important;
}

@media (max-width: 540px) {
  .tips input {
    padding: 0 !important ;
  }
  .container {
    margin-top: 70vh;
    width: 90vw;
  }
}
